  font-size: 1.2rem;
  line-height: 1.3;
  display: flex;
}

body {
  background-color: var(--background, --default-background);
  color: var(--foreground, --default-foreground);
  margin: 0;
  font-family: Libre Baskerville, serif;
  font-weight: 400;
  line-height: 2;
