}

.dark-theme, body[color-theme="dark"] {
  --background: #191919;
  --foreground: #c8c8c8;
  --non-specific-background: #3c3c3c;
  --accent-color: #1414c8;
}

.dev-theme, body[color-theme="dev"] {
  --background: #19c8c8;
  --foreground: #0a0a0a;
